<template>
  <section class = "main">
    <div class = "crumbs">
      <el-breadcrumb separator = "/">
        <el-breadcrumb-item><i class = "el-icon-rank"></i> </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class = "container">
      <div class = "plugins-tips">
        Vue.Draggable Sortable.js  Vue 
        <a href = "https://github.com/SortableJS/Vue.Draggable" target = "_blank">Vue.Draggable</a>
      </div>
      <div class = "drag-box">
        <div class = "drag-box-item">
          <div class = "item-title">todo</div>
          <draggable v-model = "todo" @remove = "removeHandle" :options = "dragOptions">
            <transition-group tag = "div" id = "todo" class = "item-ul">
              <template>
                <div v-for = "(item,index) in todo" class = "drag-list" :key = "index">
                  {{item.content}}
                </div>
              </template>
            </transition-group>
          </draggable>
        </div>
        <div class = "drag-box-item">
          <div class = "item-title">doing</div>
          <draggable v-model = "doing" @remove = "removeHandle" :options = "dragOptions">
            <transition-group tag = "div" id = "doing" class = "item-ul">
              <template>
                <div v-for = "(item,index) in doing" class = "drag-list" :key = "index">
                  {{item.content}}
                </div>
              </template>
            </transition-group>
          </draggable>
        </div>
        <div class = "drag-box-item">
          <div class = "item-title">done</div>
          <draggable v-model = "done" @remove = "removeHandle" :options = "dragOptions">
            <transition-group tag = "div" id = "done" class = "item-ul">
              <template>
                <div v-for = "(item,index) in done" class = "drag-list" :key = "index">
                  {{item.content}}
                </div>
              </template>
            </transition-group>
          </draggable>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  import draggable from 'vuedraggable'

  export default {
    name: 'draglist',
    data() {
      return {
        dragOptions: {
          animation: 120,
          scroll: true,
          group: 'sortlist',
          ghostClass: 'ghost-style'
        },
        todo: [
          {
            content: ''
          },
          {
            content: ''
          },
          {
            content: ''
          }
        ],
        doing: [
          {
            content: ''
          },
          {
            content: ''
          },
          {
            content: ''
          },
          {
            content: ''
          }
        ],
        done: [
          {
            content: ''
          },
          {
            content: ''
          }
        ]
      }
    },
    components: {
      draggable
    },
    methods: {
      removeHandle(event) {
        console.log(event);
        this.$message.success(` ${event.from.id}  ${event.to.id} `);
      }
    }
  }
</script>
<style scoped>
  .drag-box {
    display: flex;
    user-select: none;
  }
  
  .drag-box-item {
    flex: 1;
    max-width: 330px;
    min-width: 300px;
    background-color: #eff1f5;
    margin-right: 16px;
    border-radius: 6px;
    border: 1px #e1e4e8 solid;
  }
  
  .item-title {
    padding: 8px 8px 8px 12px;
    font-size: 14px;
    line-height: 1.5;
    color: #24292e;
    font-weight: 600;
  }
  
  .item-ul {
    padding: 0 8px 8px;
    height: 500px;
    overflow-y: scroll;
  }
  
  .item-ul::-webkit-scrollbar {
    width: 0;
  }
  
  .drag-list {
    border: 1px #e1e4e8 solid;
    padding: 10px;
    margin: 5px 0 10px;
    list-style: none;
    background-color: #fff;
    border-radius: 6px;
    cursor: pointer;
    -webkit-transition: border .3s ease-in;
    transition: border .3s ease-in;
  }
  
  .drag-list:hover {
    border: 1px solid #20a0ff;
  }
  
  .drag-title {
    font-weight: 400;
    line-height: 25px;
    margin: 10px 0;
    font-size: 22px;
    color: #1f2f3d;
  }
  
  .ghost-style {
    display: block;
    color: transparent;
    border-style: dashed
  }
</style>



